@charset "UTF-8";
@import url(~@fancyapps/fancybox/dist/jquery.fancybox.min.css);
#left_menu {
  position: absolute;
  z-index: 2;
  width: 100%;
  overflow: hidden;
  padding-top: 50px;
}

#handler {
  position: absolute;
  width: 100%;
  height: 3em;
  left: 0;
  top: 0;
  z-index: 1;
}

.book_page {
  font-size: 0.75em;
  font-weight: normal;
}

#slide-content {
  background: #ffffff;
  position: relative;
  width: 100%;
  height: 100%;
}

#slide-content .header {
  height: 100%;
  width: 100%;
  background: #1da1f2;
}

#slide-content .tab {
  height: 35px;
  width: 100%;
  background: #1da1f2;
}

#slide-content .tab .item {
  height: 35px;
  width: 50px;
  float: left;
  text-align: center;
  line-height: 35px;
}

#slide-content .tab .item.active {
  background: #ffffff;
  border-radius: 5px 5px 0 0;
}

/*left-panel*/
.left-panel {
  z-index: 20;
  position: relative;
}

.left-panel-toggle {
  width: 6em;
  height: 3.5em;
  position: absolute;
  left: 0;
  top: 15px;
  background-color: #fff;
  cursor: pointer;
  border-radius: 0.5em 0.5em 0 0;
  -moz-border-radius: 0.5em 0.5em 0 0;
  -webkit-border-radius: 0.5em 0.5em 0 0;
  box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, 0.1);
  transform: translate(0%, -50%);
  -moz-transform: translate(0%, -50%);
  -webkit-transform: translate(0%, -50%);
}

.left-panel-toggle svg {
  width: 1em;
  height: 1em;
  fill: #999999;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
}

.left-panel-toggle:hover svg {
  fill: #2aa1d9;
}

.left-panel .left-panel-box {
  height: 100%;
  width: 25em;
  background-color: #f8f8f8;
}

.left-panel-box .lp-head {
  background: url(../img/left_panel_header_01.jpg) no-repeat scroll left top;
}

.left-panel-box .lp-head.lv2 {
  border-bottom: 0px;
}

.left-panel-box .lp-head.lv3 {
  border-bottom: 1px solid #eaeaea;
}

.lp-head .subhead-a {
  height: 3.25em;
  display: flex;
  color: #fff;
  padding: 0 1.25em;
}

.lp-head .subhead-a div {
  width: 2.25em;
  height: 2.25em;
  margin: 0.5em 0.5em  0.5em -0.5em;
}

.lp-head .subhead-a img {
  max-width: 100%;
}

.lp-head .subhead-a p {
  font-size: 1.5em;
  line-height: 2.2em;
  font-weight: bold;
}

.lp-head .subhead-a span {
  font-size: 0.94em;
  line-height: 3.75em;
  padding-left: 0.55em;
}

.lp-head .subhead-b {
  height: 2.75em;
}

.lp-head .subhead-b ul {
  display: inline-flex;
  width: 100%;
}

.lp-head .subhead-b ul li {
  flex: 1;
  white-space: nowrap;
}

.lp-head .subhead-b ul li a {
  height: 2.75em;
  display: block;
  text-align: center;
  line-height: 2.75em;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 0.5em 0.5em 0 0;
  -moz-border-radius: 0.5em 0.5em 0 0;
  -webkit-border-radius: 0.5em 0.5em 0 0;
  transition: all 0.1s ease-in-out;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
}

.lp-head .subhead-b ul li a:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.lp-head .subhead-b ul li a.on,
.lp-head .subhead-b ul li a.on:hover {
  color: #bb6bb9;
  background-color: white;
}

.lp-head .subhead-c {
  height: 3.125em;
}

.lp-head .subhead-c ul {
  display: inline-flex;
  width: 100%;
}

.lp-head .subhead-c ul li {
  flex: 1;
  white-space: nowrap;
}

.lp-head .subhead-c ul li a {
  display: block;
  text-align: center;
  color: #666666;
  height: 3.125em;
  line-height: 3.125em;
  position: relative;
  background-color: white;
  transition: all 0.1s ease-in-out;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
}

.lp-head .subhead-c ul li a:hover {
  color: #bb6bb9;
  background-color: whitesmoke;
}

.lp-head .subhead-c ul li a:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 0.25em;
  background-color: rgba(187, 107, 185, 0);
}

.lp-head .subhead-c ul li a.on,
.lp-head .subhead-c ul li a.on:hover {
  color: #bb6bb9;
  background-color: white;
}

.lp-head .subhead-c ul li a.on:after {
  background-color: #bb6bb9;
}

