/*---------- left-panel (extend) ----------*/
.lp-itembox .photo-list .photo-box {
  float: left;
  margin-right: 0.75em;
  cursor: pointer;
}

#left_menu_shadow {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
}

/*---------- skeleton loading ----------*/
.left_menu_loading.skeleton-loading .lp-body h1,
.left_menu_loading.skeleton-loading .lp-body h2,
.left_menu_loading.skeleton-loading .lp-body p {
  background-image: linear-gradient(90deg, #F4F4F4 0px, rgba(229, 229, 229, 0.8) 40px, #F4F4F4 80px);
  background-size: 600px;
  animation: shine-lines 1.5s infinite ease-out;
  text-indent: -9999px;
  white-space: normal;
}

.left_menu_loading.skeleton-loading .lp-body img {
  content: url(../img/no-img.jpg);
}

.left_menu_loading.skeleton-loading .photo-box {
  background: url(../img/no-img.jpg);
}

@keyframes shine-lines {
  0% {
    background-position: -100px;
  }
  40%, 100% {
    background-position: 320px;
  }
}

.left_menu_loading.skeleton-loading .lp-body .tips-box p {
  background-image: linear-gradient(90deg, #fcf5cc 0px, rgba(244, 230, 163, 0.8) 40px, #fcf5cc 80px);
  background-size: 600px;
  animation: shine-lines 1.5s infinite ease-out;
  /*text-indent:-9999px;*/
}

.left_menu_loading.skeleton-loading .lp-body a {
  pointer-events: none;
  cursor: default;
}

.left_menu_loading.skeleton-loading .lp-body a.youtube_link.fancybox_btn {
  display: none;
}

@keyframes shine-lines {
  0% {
    background-position: -100px;
  }
  40%, 100% {
    background-position: 320px;
  }
}

