/*timeline*/
.timeline {
  height: 4.25em;
  /*68px*/
  z-index: 100;
  background-color: #f8f8f8;
  width: 100%;
}

.timeline-mask {
  white-space: nowrap;
}

.timeline-speech-bubble {
  position: absolute;
  background-color: #3c3c3c;
  left: 50%;
  top: -1.5em;
  font-size: 0.875em;
  height: 1.75em;
  line-height: 1.75em;
  padding: 0 0.5em;
  border-radius: 0.425em;
  -moz-border-radius: 0.425em;
  -webkit-border-radius: 0.425em;
  transform: translate(-50%, 0%);
  -moz-transform: translate(-50%, 0%);
  -webkit-transform: translate(-50%, 0%);
  box-shadow: 1px 2px 5px 0 rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 1px 2px 5px 0 rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 1px 2px 5px 0 rgba(0, 0, 0, 0.3);
  display: none;
}

.timeline-speech-bubble:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 0;
  border: 0.4em solid transparent;
  border-top-color: #3c3c3c;
  border-bottom: 0;
  margin-left: -0.4em;
  margin-bottom: -0.4em;
}

.timeline-icon-flag {
  width: 2em;
  height: 1em;
  position: absolute;
  background: url(../img/timeline-icon-flag.png) no-repeat scroll right top;
  background-size: 100%;
  left: 50%;
  top: -0.25em;
  transform: translate(-50%, 0%);
  -moz-transform: translate(-50%, 0%);
  -webkit-transform: translate(-50%, 0%);
  display: none;
}

.col-outer {
  background: url(../img/color-package/timeline_pattern_01.png) no-repeat scroll right top;
  display: inline-flex;
  color: #fff;
  height: 4.25em;
  margin-left: -1em;
  padding-right: 1em;
  position: relative;
}

.col-outer:first-child {
  margin-left: 0em;
}

.col-outer:last-child {
