html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  position: fixed;
  -webkit-overflow-scrolling: touch;
}

#app {
  width: 100%;
  height: 100%;
}

body.fancybox-active {
  position: unset;
}

#map {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}

/*default setting*/
* {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  line-height: 130%;
}

html {
  font-size: 16px;
  font-family: arial,"Microsoft JhengHei","",sans-serif;
}

.body {
  width: 100%;
  min-width: 768px;
  background-color: #fff;
  height: 100vh;
  position: relative;
  background: url(../img/map-bg.jpg) no-repeat scroll center center;
  background-size: cover;
}

.txt-link,
.txt-highlight {
  color: #2aa1d9;
}

.youtube_link {
  text-decoration: underline;
  cursor: pointer;
}

.h-bookmark {
  display: inline-flex;
  justify-content: space-between;
  width: 100%;
}

.lp-itembox .h-bookmark h1,
.h-bookmark h1 {
  margin-bottom: 0;
  flex: 2;
  line-height: 1.5em;
}

/*---------- tips box ----------*/
.tips-box {
  border: 1px solid #f4e59e;
  background-color: #ffffef;
  padding: 1em !important;
  border-radius: 0.5em;
  -moz-border-radius: 0.5em;
  -webkit-border-radius: 0.5em;
}

.tips-box .title {
  font-weight: bold;
  color: #e9a30e;
  position: relative;
  padding-bottom: 0.5em;
}

.tips-box .title:before {
  content: '';
  position: absolute;
  width: 1.5em;
  height: 1.5em;
  background: url(../img/icon-tips.png) no-repeat scroll left top;
  background-size: 1.5em;
}

.tips-box .title p {
  padding-left: 2em;
  font-size: 1.125em;
}

.tips-box > p {
  font-size: 0.875em;
}

/*---------- warning box ----------*/
.warning-box {
  border: 1px solid #edb0aa;
  background-color: #fff5f5;
  padding: 1em !important;
  border-radius: 0.5em;
  -moz-border-radius: 0.5em;
  -webkit-border-radius: 0.5em;
}

.warning-box .title {
  font-weight: bold;
  color: #ed7569;
  position: relative;
  padding-bottom: 0.5em;
}

.warning-box .title p {
  font-size: 1.125em;
}

.warning-box > p {
  font-size: 0.875em;
}

/*---------- class list table ----------*/
.class-list-table {
  display: table;
  width: 100%;
}

.class-list-table .table-tr {
  display: table-row;
}

.class-list-table .table-tr .table-td {
  display: table-cell;
}

.class-list-table .table-tr .table-td p {
  text-align: center;
  line-height: 2.25em;
  font-size: 0.875em;
  height: 2.25em;
  cursor: pointer;
  background-color: white;
  border: 1px solid #d4d4d4;
  margin: 0 0.3em 0.3em 0;
  border-radius: 0.5em;
  -moz-border-radius: 0.5em;
  -webkit-border-radius: 0.5em;
  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
}

.class-list-table .table-tr .table-td:last-child p {
  margin-right: 0;
}

.class-list-table .table-tr:last-child .table-td p {
  margin-bottom: 0;
}

.class-list-table .table-tr .table-td p.on,
.class-list-table .table-tr .table-td p.on:hover {
  background-color: #2aa1d9;
  border: 1px solid #2aa1d9;
  color: white;
}

.class-list-table .table-tr .table-td p:hover {
  border: 1px solid #2aa1d9;
  color: #2aa1d9;
}

/*---------- comp data bar (/ bar) ----------*/
.comp-data-bar {
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
}

.comp-data-bar > div {
  background-color: #f4f4f4;
  border-radius: 0.5em;
  -moz-border-radius: 0.5em;
  -webkit-border-radius: 0.5em;
  width: 100%;
  margin-right: 0.5em;
  text-align: center;
  padding: 0.75em 0;
}

.comp-data-bar > div:last-child {
  margin: 0;
}

.comp-data-bar div span {
  display: block;
  line-height: 100%;
  font-size: 0.875em;
}

.comp-data-bar div p {
  line-height: 100%;
  font-size: 1.75em;
  margin-top: 0.25em;
}

.comp-data-bar div b.percent {
  font-weight: normal;
  font-size: 0.65em;
  padding-left: 0.125em;
}

.comp-data-bar.small > div {
  padding: 0.5em 0;
  margin-right: 0.25em;
}

.comp-data-bar.small div p {
  font-size: 1.25em;
}

/*---------- map UI ----------*/
.menu-general {
  position: absolute;
}

.menu-general .menu-btn {
  width: 2.5em;
  height: 2.5em;
  display: block;
  margin-left: 1em;
  background-color: #fff;
  float: right;
  position: relative;
  text-align: center;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  box-shadow: 1px 3px 4px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 1px 3px 4px 0px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 1px 3px 4px 0px rgba(0, 0, 0, 0.1);
}

.menu-general .menu-btn svg {
  width: 1.25em;
  height: 1.25em;
  position: absolute;
  top: 50%;
  left: 50%;
  fill: #999;
  transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
}

.menu-general .menu-btn:hover svg {
  fill: #2aa1d9;
}

/*---------- normal action button ----------*/
.btn-action {
  padding: 0 0.75em;
  display: inline-flex;
  color: #333;
  height: 2em;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  line-height: 2em;
  float: right;
  margin-left: 0.5em;
  border-radius: 0.5em;
  -moz-border-radius: 0.5em;
  -webkit-border-radius: 0.5em;
  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
}

.btn-action svg {
  width: 1em;
  height: 1em;
  margin: 0.45em 0.45em 0.45em 0;
  fill: #999;
  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
}

.btn-action p {
  font-size: 0.94em;
  flex: 2;
  line-height: 2em !important;
  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
}

.btn-action:hover {
  border: 1px solid #2aa1d9;
