.btn-action.bookmark:hover {
  color: #ee6b68;
}

.btn-action.bookmark.on {
  border: 1px solid #ee6b68;
  background-color: #ee6b68;
}

.btn-action.bookmark.on svg {
  fill: #fff;
}

.btn-action.bookmark.on {
  color: #fff;
}

.lp-itembox .btn-action {
  margin-left: 1em;
}

.btn-solid {
  font-size: 1.125em;
  padding: 0 0.75em;
  display: block;
  color: #fff;
  height: 2.25em;
  background-color: #fff;
  line-height: 2.25em;
  width: 100%;
  background-color: #2aa1d9;
  font-weight: bold;
  text-align: center;
  border-radius: 0.5em;
  -moz-border-radius: 0.5em;
  -webkit-border-radius: 0.5em;
  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
}

.btn-solid:hover {
  background-color: #2387b9;
}



