.map-control{
    position:absolute;
    right: 0.6em;
    bottom: 10em;
}
.map-control .map-filter-btn,
.map-control .map-scale,
.map-control .map-time{
    display:block;
    position:relative;
    background-color:#fff;
    border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
    box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    -moz-box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    -webkit-box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
}
.map-control .map-filter{
    display:flex;
    flex-direction:column;
}
.map-control .map-filter-btn{
    width:2em;
    height:2em;
    margin-bottom: 1em;
}
.map-control .map-scale a{
    width:2em;
    height:2em;
    display:flex;
    position:relative;
    flex-direction:column;
}
.map-control .map-filter-btn svg,
.map-control .map-scale a svg{
    width:1em;
    height:1em;
    position:absolute;
    top:50%;
    left:50%;
    fill:#999;
    transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.map-control .map-filter-btn:hover svg,
.map-control .map-scale a:hover svg{
    fill:rgba(42, 161, 217, 1)
;
}
.map-control .map-scale a:first-child{border-bottom:1px solid #eaeaea;}


.map-control .map-time {
    position:absolute;
    bottom: -80px;
    white-space:nowrap;
    right:3em;
    height:2em;
    line-height:2em;
    padding:0 0.75em;
}
.map-control .map-time p {
    font-size:0.75em;
    line-height:2.75em;
    color:#999;
}

/*popup filter*/
.popup-filter{
    z-index: 1;
    width:16.88em;
    color:#333;
    background-color:#fff;
    border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
    box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    -moz-box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    -webkit-box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
}
.popup-filter>div{
    padding:1em;
}
.popup-filter h1{
    font-size:0.95em;
    padding-bottom:0.75em;
}
.popup-filter .col-2 .select-gp{
    display:inline-flex;
    flex-wrap:wrap;
    width:100%;
}
.popup-filter .col-1 .select-gp a{
    flex:1;
}
.popup-filter .col-1 .select-gp a,
.popup-filter .col-2 .select-gp a{
    white-space:nowrap;
    display:block;
    text-align:center;
    margin-right:0.75em;
}
.popup-filter .col-2 .select-gp a:last-child,
.popup-filter .col-2 .select-gp a:nth-child(3){
    margin-right:0;
}
.popup-filter .select-gp p{
    text-align:center;
    font-size:0.7em;
    color:#666;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.popup-filter .select-gp img{
    max-width:100%;
}
.popup-filter .col-1 .select-gp a{
    /*width:6.875em;*/
    width:7em;
    float:left;
    margin-bottom:0.5em;
}
.popup-filter .col-1 .select-gp a:nth-child(even){
    margin-right:0;
}
.popup-filter .col-1 .select-gp img{
    border:2px solid #fff;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.popup-filter .col-1 .select-gp a.on p{
    color:rgba(42, 161, 217, 1);
}
.popup-filter .col-1 .select-gp a.on img{
    border:2px solid rgba(42, 161, 217, 1);
}
.popup-filter .col-2 .select-gp img{
    width:3.125em;
}
.popup-filter .col-2 .select-gp a{
    padding:0.5em;
    margin-top:0.125em;
    margin-bottom:0.125em;
    background-color:#fff;
    border:1px solid #fff;
    border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.popup-filter .col-2 .select-gp a.on{
    border:1px solid rgba(42, 161, 217, 1);
    background-color:rgba(42, 161, 217, 0.1);
}
.popup-filter .col-2 .select-gp a.on p{
    color:rgba(42, 161, 217, 1);
}
.popup-filter>div.col-1{
    border-bottom:1px solid #eaeaea;
    overflow:hidden;
    padding-bottom:0.5em;
}


.popup-filter {
    position:absolute;
    bottom:3.5em;
    right:0;
}



