.menu-general{
    position:absolute;
    right:1em;
    top:1em;
    z-index: 1;
}
.menu-general .menu-btn{
    width:2.5em;
    height:2.5em;
    display:block;
    margin-left:1em;
    background-color:#fff;
    float:right;
    position:relative;
    text-align:center;
    border-radius:50%;
    -moz-border-radius:50%;
    -webkit-border-radius:50%;
    box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    -moz-box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    -webkit-box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
}
.menu-general .menu-btn svg{
    width:1.25em;
    height:1.25em;
    position:absolute;
    top:50%;
    left:50%;
    fill:#999;
    transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.menu-general .menu-btn:hover svg{
    fill:rgba(42, 161, 217, 1);
}

/*popup usermenu*/
.popup-usermenu{
    width:16.88em;
    color:#333;
    background-color:#fff;
    border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
    box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    -moz-box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    -webkit-box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    z-index: 1;
}
.popup-usermenu>div{
    padding:1em;
}
.popup-usermenu .user-profile{
    width:100%;
    display:inline-flex;
    background-color:#f8f8f8;
    border-bottom:1px solid #eaeaea;
    border-radius:0.5em 0.5em 0 0;
    -moz-border-radius:0.5em 0.5em 0 0;
    -webkit-border-radius:0.5em 0.5em 0 0;
}
.popup-usermenu .user-img{
    width:3.5em;
    height:3.5em;
    margin-right:1em;
    background-color:#dddddd;
    border-radius:50%;
    -moz-border-radius:50%;
    -webkit-border-radius:50%;
}
.popup-usermenu .user-img img{
    width:100%;
    border-radius:50%;
    -moz-border-radius:50%;
    -webkit-border-radius:50%;
}
.popup-usermenu .user-name{
    flex:2;
}
.popup-usermenu .user-profile h1{
    font-size:1.125em;
    margin:0.2em 0 0.35em 0;
}
.popup-usermenu .user-profile .btn-action{
    font-size:0.9em;
    padding:0 0.6em;
    height:2em;
    line-height:2em;
    float:left;
    margin-left:0;
}
.popup-usermenu .user-menu{
    padding-left:0;
    padding-right:0;
}
.popup-usermenu .user-menu a{
    display:block;
    padding:0.6em 1em;
    color:#333;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.popup-usermenu .user-menu a:hover{
    color:rgba(42, 161, 217, 1);
    background-color:rgba(248, 248, 248, 1);
}



