/*---------- left-panel (search) ----------*/
.left-panel .left-panel-box.search{
    background-color:#fff;
}
.left-panel-box.search .lp-itembox{
    margin-bottom:0;
    border-top:0;
}
.search-title{
    background-color:#ebebeb;
    padding:0 1.25em;
}
.search-title p{
    color:#666;
    line-height:2.125em;
    font-size:0.95em;
}
.search-title span{
    padding:0.25em;
}
.search-box{
    background-color:#f8f8f8;
    padding:0.625em;
}
.search-box .search-field{
    border:1px solid #d4d4d4;
    background-color:#fff;
    display:inline-flex;
    height:2.5em;
    width:100%;
    border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
}
.search-box .search-field>div:first-child{
    display:inline-flex;
    flex:2;
    padding:0 3em 0 0.5em;
    position:relative;
}
.search-box .search-submit{
    position:relative;
    width:4em;
    border-left:1px solid #d4d4d4;
    cursor:pointer;
    background-color:#fff;
    border-radius:0 0.5em 0.5em 0;
    -moz-border-radius:0 0.5em 0.5em 0;
    -webkit-border-radius:0 0.5em 0.5em 0;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.search-box .search-submit:hover{
    background-color:#f8f8f8;
}
.search-box .search-submit svg{
    width:1.25em;
    height:1.25em;
    position:absolute;
    top:50%;
    left:50%;
    fill:#999;
    transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;}
.search-box .search-submit:hover svg{
    fill:#666;
}
.search-box .input-search{
    font-size:0.95em;
    padding:0 0.5em;
    width:100%;
    color:#333;
}
::-webkit-input-placeholder{
    color:#b1b1b1;
}
::placeholder{
    color:#b1b1b1;
}
:-ms-input-placeholder{
    color:#b1b1b1;
}
.search-box .search-cancel{
    width:1.25em;
    height:1.25em;
    background-color:red;
    position:absolute;
    right:1em;
    top:50%;
    cursor:pointer;
    background-color:#f8f8f8;
    border:1px solid #d4d4d4;
    border-radius:50%;
    -moz-border-radius:50%;
    -webkit-border-radius:50%;
    transform:translate(0%, -50%);
    -moz-transform:translate(0%, -50%);
    -webkit-transform:translate(0%, -50%);
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.search-box .search-cancel:hover{
    background-color:#d4d4d4;
}
.search-box .search-cancel svg{
    fill:#999;
    width:0.5em;
    height:0.5em;
    position:absolute;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.search-box .search-cancel:hover svg{
    fill:#fff;
}
.search-record{cursor:pointer;width:100%;padding:0.75em 1.25em;background-color:#fff;display:inline-flex;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;}
.search-record svg{width:1em;height:1em;fill:#999;margin-right:0.75em;}
.search-record p{flex:2;}
.search-record:hover{background-color:rgba(248, 248, 248, 1);}
.search-record:first-child{margin-top:0.75em;}



.search-title.skeleton-loading p,
.search_result.skeleton-loading h1,
.search_result.skeleton-loading h2,
.search_result.skeleton-loading p {
    background-image:linear-gradient(90deg, #F4F4F4 0px, rgba(229,229,229,0.8) 40px, #F4F4F4 80px);
    background-size:600px;
    animation:shine-lines 1.5s infinite ease-out;
    text-indent:-9999px;
    white-space: normal;
}

.search_result.skeleton-loading img {
    content:url(../img/no-img.jpg);
}

.search_result.skeleton-loading .photo-box {
    background: url(../img/no-img.jpg);
}
.search-default-box{
    height:100%;
    width:100%;
    position:relative;
}
.search-default-box div{
    text-align:center;
    position:absolute;
    top:50%;
    left:50%;
    width:18em;
    transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
}
.search-default-box img{
    width:6.25em;
    height:6.25em;
}
.search-default-box p{
    line-height:150%;
    color:#999;
    font-size:0.94em;
    margin-top:1em;
}

#left_menu .search_result p.description {

    display: block; /* Fallback for non-webkit */
    display: -webkit-box;
    height: 50px; /* Fallback for non-webkit */
    margin: 0 auto;
    line-height: 25px;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: pre-line;
    margin-bottom: 0.6em;
}

#left_menu.extend .search_result p.description {
    display: block; /* Fallback for non-webkit */
    display: -webkit-box;
    height: unset; /* Fallback for non-webkit */
    margin: 0 auto;
    line-height: unset;
    -webkit-line-clamp: unset;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: pre-line;
    margin-bottom: 0.6em;
}



