/*popup general*/
.dark-fullmask{
    width: 100%;
    height: 100%;
    background-color:rgba(0, 0, 0, 0.3);
    z-index:500;
    position:absolute;
    left:0;
    top:0;
}
.popup-general-outbox{
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding: 1.5em 1em;
    position:absolute;
    left:50%;
    top:50%;
    background-color: #fff;
    border-radius: 0.75em;
    -moz-border-radius: 0.75em;
    -webkit-border-radius: 0.75em;
    box-shadow: 1px 3px 4px 0px rgba(0,0,0,0.1);
    -moz-box-shadow: 1px 3px 4px 0px rgba(0,0,0,0.1);
    -webkit-box-shadow: 1px 3px 4px 0px rgba(0,0,0,0.1);
    transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
    z-index: 501;
}
.popup-general{
    height:90%;
    overflow: hidden;
}
.popup-general h1{
    font-size:1.25em;
    margin-bottom:0.8em;
}
.popup-general p{
    line-height:150%;
}
.popup-general .des-text p{
    line-height:140%;
    color:#999;
    font-size:0.94em;
    margin-bottom:0.285em;
}
.popup-general .des-text p:last-child{
    padding-bottom:0;
}
.popup-general>*{
    margin-bottom:1em;
}
.popup-general>*:last-child{
    margin-bottom:0;
}
.popup-general .photo-list{
    overflow:hidden;
}
.popup-general .photo-list .photo-box{
    width:100%;
    height:240px;
    background-color:#999;
    margin-bottom:0.75em;
    /*float:left;
    margin-right:0.75em;*/
}
.popup-general .photo-list .photo-box:last-child{
    margin-bottom:0;
}
.popup-general-head{
    display:inline-flex;
    width:100%;
    justify-content:space-between;
    margin-bottom:1em;
}
.popup-general-head .btn-action{
    vertical-align:top;
}
.popup-general-head h1{
    font-size:1.5em;
    margin-bottom:0;
    flex:2;
}
.popup-general-head .head-action{
    margin-left:1em;
}

.popup-media-description {
  position: relative;

      margin-bottom: 10px;
}
.popup-media-iframe {
  position: relative;
  height: 90%;
}



