
.info-title {
    background-color: #ebebeb;
    padding: 0 1.25em;
}

.info-title p {
    color: #666;
    line-height: 2.125em;
    font-size: 0.95em;
}

/*---------- left-panel (bookmark) ----------*/
.listbox-remove{
    width:2.25em;
    position:relative;
    margin-left:1.25em;
}

.listbox-remove div{
    cursor:pointer;
    position:absolute;
    right:0;
    top:50%;
    padding:0.5em;
    background-color:#eee;
    width:2.25em;
    height:2.25em;
    border-radius:50%;
    -moz-border-radius:50%;
    -webkit-border-radius:50%;
    transform:translate(0%, -50%);
    -moz-transform:translate(0%, -50%);
    -webkit-transform:translate(0%, -50%);
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.listbox-remove div svg{
    width:1.25em;
    height:1.25em;
    fill:#999;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}

.lp-listbox.remove:hover{
    background-color:#fff;
    cursor:default;
}
.lp-listbox.remove, .listbox-remove{
    display:block;
}
.lp-listbox.remove, .listbox-remove div:hover{
    background-color:#ee6b68;
}
.lp-listbox.remove, .listbox-remove div:hover svg{
    fill:#fff;
}




/*---------- left-panel (bookmark) ----------*/
.lp-head-info{
    justify-content:space-between;
    padding:0.75em 1.25em;
    height:3.5em;
    border-bottom:1px solid #d4d4d4;
    width:100%;
    display:inline-flex;
}
.lp-head-info h1{
    font-size:1.25em;
    line-height:1.6em;
}
.lp-head-info .btn-normal{
    padding:0 0.75em;
    color:#333;
    height:2em;
    border:1px solid #d4d4d4;
    display:inline-block;
    background-color:#fff;
    line-height:2em;
    border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.lp-head-info .btn-normal:hover{
    border:1px solid rgba(42, 161, 217, 1);
    color:rgba(42, 161, 217, 1);
}


#left_menu .bookmark p.description {

    display: block; /* Fallback for non-webkit */
    display: -webkit-box;
    height: 50px; /* Fallback for non-webkit */
    margin: 0 auto;
    line-height: 25px;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: pre-line;
    margin-bottom: 0.6em;
}

#left_menu.extend .bookmark p.description {
    display: block; /* Fallback for non-webkit */
    display: -webkit-box;
    height: unset; /* Fallback for non-webkit */
    margin: 0 auto;
    line-height: unset;
    -webkit-line-clamp: unset;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: pre-line;
    margin-bottom: 0.6em;
}




