.body{
    min-width:100vw;
    overflow:hidden;
}
body, html{
    overflow: hidden;
    position: fixed;
    -webkit-overflow-scrolling: touch;
}
.timeline{
    bottom:auto;
    position:relative;
    display:inherit;
}
.left-panel{
    width:100%;
    /*top:200px;*/
}
.left-panel.pos-bottom{
    top:100%;
    margin-top:-192px;
    /*margin-top:-116px;*/
}
.left-panel.pos-middle{
    top:30%;
}
.left-panel.pos-top{
    top:0%;
    margin-top:-48px;
}
.left-panel .left-panel-box{
    width:100%;
}
#mobile_content_popup {
    position: absolute;
    z-index: 200;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.left-panel-toggle{
    display:none;
}
#right_bottom_menu {
    display:none;
}
.lp-head .subhead-a{
    display:none;
}
.lp-head .subhead-b ul li a{
    border-radius:0;
    -moz-border-radius:0;
    -webkit-border-radius:0;
}
.lp-head .subhead-b{
    border-top:1px solid #fff;
}
.lp-itembox .ppl-gridbox{
    width:28%;
}
.lp-itembox .photo-list .photo-box{
    background-position:center center;
    background-color:#f4f4f4;
}
.map-control{
    position:inherit;
    right: 0;
    top: 0;
    height:3em;
}
.map-control .map-filter{
    display:none;
}
.map-control .map-time{
    position:relative;
    bottom:auto;
    right:auto;
    margin:0.5em;
    float:right;
}
.map-control .map-time p{
    text-align:right;
}
.menu-general{
    width:100%;
    padding:1em;
    position:absolute;
    right:0;
    top:0;
}
.menu-general .menu-btn{
    float:right;
}
.menu-general .menu-btn.mainmenu{
    float:left;
    margin-left:0;
    margin-right:0;
}
.bottom-panel-toggle{
    display: inline-flex;
    position: relative;
    margin: 0.5em;
}
.bottom-panel-toggle {
    position: absolute;
    width:4em;
    height:2em;
    left:0;
    top:0;
    background-color:#fff;
    cursor:pointer;
    border-radius:0 0.5em 0.5em 0;
    -moz-border-radius:0 0.5em 0.5em 0;
    -webkit-border-radius:0.5em 0.5em;
    box-shadow:3px 3px 4px 0px rgba(0,0,0,0.1);
    -moz-box-shadow:3px 3px 4px 0px rgba(0,0,0,0.1);
    -webkit-box-shadow:3px 3px 4px 0px rgba(0,0,0,0.1);
}
.bottom-panel-toggle svg{
    width:1em;
    height:1em;
    fill:#999999;
    position:absolute;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.bottom-panel-toggle:hover svg {
    fill: rgba(42, 161, 217, 1);
}


/*---------- popup-filter ----------*/
.popup-filter{
    z-index:500;
    position:absolute;
    left:0;
    top:0;
    width:100%;
    height:100vh;
    overflow:auto;
    border-radius:0;
    -moz-border-radius:0;
    -webkit-border-radius:0;
    box-shadow:0px rgba(0,0,0,0.1);
    -moz-box-shadow:0px rgba(0,0,0,0.1);
    -webkit-box-shadow:0px rgba(0,0,0,0.1);
}
.popup-filter>div.col-1{
    padding:1.25em;
    padding-bottom:0.75em;
}
.popup-filter>div{
    padding:1.25em;
}
.popup-filter .col-1 .select-gp a{
    width:47%;
}
.popup-filter .select-gp p{
    font-size:0.9em;
}
.popup-filter .col-1 .select-gp a{
    margin-bottom:0.75em;
}
.popup-filter h1{
    font-size:1.25em;
}
.popup-filter .col-2 .select-gp img{
    width:3.25em;
}
.popup-filter .col-2 .select-gp a{
    margin-top:0.25em;
    margin-bottom:0.25em;
    flex:0 0 30%;
    box-sizing:border-box;
}
.popup-filter .col-2 .select-gp a,
.popup-filter .col-2 .select-gp a:last-child,
.popup-filter .col-2 .select-gp a:nth-child(3){
    margin-right:3%;
}
.popup-filter .menu-btn-close {
    display:block;
    width:1em;
    height:1em;
    margin:0.25em;
    fill:#999999;
    position:absolute;
    right:1.25em;
    top:1.375em;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.popup-filter .menu-btn-close:hover {
    fill: rgba(42, 161, 217, 1);
}

.popup-usermenu{
    z-index:300;
    position:absolute;
    top:0;
    left:0;
    right:0;
    width:100%;
    height:100%;
    border-radius:0;
    -moz-border-radius:0;
    -webkit-border-radius:0;
}
.popup-usermenu .user-menu{
    padding:0;
}
.popup-usermenu .user-menu a{
    padding: 1.125em 1em;
    border-bottom:1px solid #eaeaea;
}

.popup-usermenu .menu-btn-close{
    display:block;
    width:1em;
    height:1em;
    margin:0.25em;
    fill:#999999;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.popup-usermenu .menu-btn-close:hover{
    fill:rgba(42, 161, 217, 1);
}


/*mobile search bar*/
.search-box-mobile{
    background-color:#f8f8f8;
    padding:0.625em;
    height:3.75em;
}
.search-box-mobile .search-field{
    border:1px solid #d4d4d4;
    background-color:#fff;
    display:inline-flex;
    height:2.5em;
    width:100%;
    border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
}
.search-box-mobile .search-field>div.search-txt{
    display:inline-flex;
    flex:2;
    padding:0 3em 0 0.5em;
    position:relative;
}
.search-box-mobile .search-submit{
    position:relative;
    width:3.5em;
    border-right:1px solid #d4d4d4;
    cursor:pointer;
    background-color:#fff;
    border-radius:0.5em 0 0 .5em;
    -moz-border-radius:0.5em 0 0 .5em;
    -webkit-border-radius:0.5em 0 0 .5em;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.search-box-mobile .search-submit:hover{
    background-color:#f8f8f8;
}
.search-box-mobile .search-submit svg{
    width:1.25em;
    height:1.25em;
    position:absolute;
    top:50%;
    left:50%;
    fill:#999;
    transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;}
.search-box-mobile .search-submit:hover svg{
    fill:#666;
}
.search-box-mobile .input-search{
    font-size:0.95em;
    padding:0 0.5em;
    width:100%;
    color:#333;
}
::-webkit-input-placeholder{
    color:#b1b1b1;
}
::placeholder{
    color:#b1b1b1;
}
:-ms-input-placeholder{
    color:#b1b1b1;
}
.search-box-mobile .search-cancel{
    width:1.25em;
    height:1.25em;
    background-color:red;
    position:absolute;
    right:0.75em;
    top:50%;
    cursor:pointer;
    background-color:#f8f8f8;
    border:1px solid #d4d4d4;
    border-radius:50%;
    -moz-border-radius:50%;
    -webkit-border-radius:50%;
    transform:translate(0%, -50%);
    -moz-transform:translate(0%, -50%);
    -webkit-transform:translate(0%, -50%);
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.search-box-mobile .search-cancel:hover{
    background-color:#d4d4d4;
}
.search-box-mobile .search-cancel svg{
    fill:#999;
    width:0.5em;
    height:0.5em;
    position:absolute;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.search-box-mobile .search-cancel:hover svg{
    fill:#fff;
}
.search-box-mobile .search-cancel{
    width:1.5em;
    height:1.5em;
}
.search-box-mobile .search-cancel svg{
    width:0.6em;
    height:0.6em;
}
.btn-action svg{
    margin-right:0;
}



