nav {
    display: flex;
    flex-direction: row;
}
/*timeline*/
.timeline{
    height:4.25em; /*68px*/
    //position:absolute;
    //bottom:0;
    z-index: 100;
    background-color:#f8f8f8;
    width: 100%;
}
.timeline-mask{
    white-space:nowrap;
}
.timeline-speech-bubble{
    position:absolute;
    background-color:#3c3c3c;
    left:50%;
    top:-1.5em;
    font-size:0.875em;
    height:1.75em;
    line-height:1.75em;
    padding:0 0.5em;
    border-radius:0.425em;
    -moz-border-radius:0.425em;
    -webkit-border-radius:0.425em;
    transform:translate(-50%, 0%);
    -moz-transform:translate(-50%, 0%);
    -webkit-transform:translate(-50%, 0%);
    box-shadow:1px 2px 5px 0 rgba(0, 0, 0, .3);
    -moz-box-shadow:1px 2px 5px 0 rgba(0, 0, 0, .3);
    -webkit-box-shadow:1px 2px 5px 0 rgba(0, 0, 0, .3);
    display: none;
}
.timeline-speech-bubble:after {
    content:'';
    position:absolute;
    bottom:0;
    left:50%;
    width:0;
    height:0;
    border:0.4em solid transparent;
    border-top-color:#3c3c3c;
    border-bottom:0;
    margin-left:-0.4em;
    margin-bottom:-0.4em;
}
.timeline-icon-flag {
    width:2em;
    height:1em;
    position:absolute;
    background:url(../img/timeline-icon-flag.png) no-repeat scroll right top;
    background-size:100%;
    left:50%;
    top:-0.25em;
    transform:translate(-50%, 0%);
    -moz-transform:translate(-50%, 0%);
    -webkit-transform:translate(-50%, 0%);
    display: none;

}
.col-outer {
    background:url(../img/color-package/timeline_pattern_01.png) no-repeat scroll right top;
    display:inline-flex;
    color:#fff;
    height:4.25em;
    margin-left:-1em;
    padding-right:1em;
    position: relative;
}
.col-outer:first-child{margin-left:0em;}
.col-outer:last-child{background-position:left top;}
.col-outer:nth-child(18){z-index:266;}
.col-outer:nth-child(17){z-index:268;}
.col-outer:nth-child(16){z-index:270;}
.col-outer:nth-child(15){z-index:272;}
.col-outer:nth-child(14){z-index:274;}
.col-outer:nth-child(13){z-index:276;}
.col-outer:nth-child(12){z-index:278;}
.col-outer:nth-child(11){z-index:280;}
.col-outer:nth-child(10){z-index:282;}
.col-outer:nth-child(9){z-index:284;}
.col-outer:nth-child(8){z-index:286;}
.col-outer:nth-child(7){z-index:288;}
.col-outer:nth-child(6){z-index:290;}
.col-outer:nth-child(5){z-index:292;}
.col-outer:nth-child(4){z-index:294;}
.col-outer:nth-child(3){z-index:296;}
.col-outer:nth-child(2){z-index:298;}
.col-outer:nth-child(1){z-index:300;}
.col-outer p{font-size:1.125em;text-align:center;font-weight:bold;white-space:nowrap;line-height:1;}
.col-outer span{font-size:0.875em;text-align:center;display:block;white-space:nowrap;line-height:1;padding-top:0.5em;}
.col-outer img{width:3.125em;height:3.75em;margin-top:0.5em;}
.col-outer .tc-head{
    display:inline-flex;
    margin-left:1.75em;
    cursor:pointer;
}
.col-outer:first-child .tc-head{margin-left:1em;}
.col-outer .tc-head .name-area{padding:1em 1em;}
.col-outer .tc-tail{
    display:inherit;
    padding:0.25em 0;
}
.col-outer .tc-tail .col-inner{
    display:inherit;
    border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
}
.col-outer .tc-tail .col-inner>div{
    padding:0.72em 1em;
    position:relative;
    height:3.75em;
    cursor:pointer;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.col-outer .tc-tail .col-inner>div,
.col-outer .tc-tail .col-inner>div{
    background-color:rgba(185,105,185,1);
}
.col-outer .tc-tail .col-inner>div:hover,
.col-outer .tc-tail .col-inner>div.on{
    background-color:rgba(165,85,165,1); /*each point -20*/
}
.col-outer .tc-tail .col-inner>div:first-child{
    border-radius:0.5em 0 0 0.5em;
    -moz-border-radius:0.5em 0 0 0.5em;
    -webkit-border-radius:0.5em 0 0 0.5em;
}
.col-outer .tc-tail .col-inner>div:last-child{
    border-radius:0 0.5em 0.5em 0;
    -moz-border-radius:0 0.5em 0.5em 0;
    -webkit-border-radius:0 0.5em 0.5em 0;
}
.col-outer .tc-tail .col-inner>div:after{
    content:'';
    height:1.75em;
    width:1px;
    position:absolute;
    right:0;
    top:1em;
    background-color:rgba(255,255,255,0.2);
}
.col-outer .tc-tail .col-inner>div:last-child:after{
    display:none;
}
.col-outer .tc-tail .shorten{
    margin:0 0.5em;
    position:relative;
    height:3.75em;
    cursor:pointer;
    float:left;
    width:1.75em;
    background-color: rgba(185,105,185,1);
    border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
    transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.col-outer .tc-tail .shorten:hover{
    background-color: rgba(165,85,165,1);
}
.shorten svg{
    width:0.75em;
    height:0.75em;
    fill:#fff;
    position:absolute;
    left:50%;
    top:50%;
    transform:translate(-50%, -50%);
    -moz-transform:translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
}

.col-outer .tc-tail .shorten.close_timeline_btn {
    margin-left: 0;
}


