/*popup filter*/
.popup-filter{
	width:16.88em;
	color:#333;
	background-color:#fff;
    border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
    box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    -moz-box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
    -webkit-box-shadow:1px 3px 4px 0px rgba(0,0,0,0.1);
}
.popup-filter>div{
	padding:1em;
}
.popup-filter h1{
	font-size:0.95em;
	padding-bottom:0.75em;
}
.popup-filter .col-2 .select-gp{
	display:inline-flex;
	flex-wrap:wrap;
	width:100%;
}
.popup-filter .col-1 .select-gp a{
	flex:1;
}
.popup-filter .col-1 .select-gp a,
.popup-filter .col-2 .select-gp a{
	white-space:nowrap;
	display:block;
	text-align:center;
	margin-right:0.75em;
}
.popup-filter .col-2 .select-gp a:last-child,
.popup-filter .col-2 .select-gp a:nth-child(3n){
    margin-right:0;
}
.popup-filter .select-gp p{
	text-align:center;
	font-size:0.7em;
	color:#666;
	transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.popup-filter .select-gp img{
	max-width:100%;
}
.popup-filter .col-1 .select-gp a{
	/*width:6.875em;*/
    width:7em;
    float:left;
    margin-bottom:0.5em;
}
.popup-filter .col-1 .select-gp a:nth-child(even){
	margin-right:0;
}
.popup-filter .col-1 .select-gp img{
    width: 100%;
	border:2px solid #fff;
	transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.popup-filter .col-1 .select-gp a.on p{
	color:rgba(42, 161, 217, 1);
}
.popup-filter .col-1 .select-gp a.on img{
	border:2px solid rgba(42, 161, 217, 1);
}
.popup-filter .col-2 .select-gp img{
	width:3.125em;
}
.popup-filter .col-2 .select-gp a{
	padding:0.5em;
	margin-top:0.125em;
	margin-bottom:0.125em;
	background-color:#fff;
	border:1px solid #fff;
	border-radius:0.5em;
    -moz-border-radius:0.5em;
    -webkit-border-radius:0.5em;
	transition:all 0.2s ease-in-out;
    -webkit-transition:all 0.2s ease-in-out;
    -moz-transition:all 0.2s ease-in-out;
}
.popup-filter .col-2 .select-gp a.on{
	border:1px solid rgba(42, 161, 217, 1);
	background-color:rgba(42, 161, 217, 0.1);
}
.popup-filter .col-2 .select-gp a.on p{
	color:rgba(42, 161, 217, 1);
}
.popup-filter>div.col-1{
	border-bottom:1px solid #eaeaea;
	overflow:hidden;
	padding-bottom:0.5em;
}







