#hkep_msg_pop .popup-box {
    background-color: #fff;
    position: fixed;
    z-index: 1060;
    left: 50%;
    top: 50%;
    padding: 3rem 5rem;
    width: 90%;
    max-width: 500px;
    -webkit-box-shadow: -2px -2px 12px 0px rgba(0, 0, 0, 0.1);
    box-shadow: -2px -2px 12px 0px rgba(0, 0, 0, 0.1);
    -webkit-radius: 6px;
    -moz-border-radiust: 6px;
    border-radius: 6px;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

#hkep_msg_pop .btn-close.dark {
    display: block;
    width: 1.25rem;
    height: 1.25rem;
    position: absolute;
    right: 32px;
    top: 32px;
    fill: #afc0cb;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

#hkep_msg_pop .dark-mask {
    background-color: #1d3648;
    opacity: 0.7;
    width: 100vw;
    height: 100vh;
    z-index: 59;
    position: fixed;
    left: 0;
    top: 0;
}

#hkep_msg_pop #alert_img {
    width: 60px;
    height: 60px;
    margin-bottom: 16px;
}

#hkep_msg_pop #hkep_msg_pop_content {
    overflow-y: auto;
    max-height: 300px;
    width: 100%;
    margin-top: 15px;
    padding: 0 5px 0 5px;
}

#hkep_msg_pop *{
    padding:0;
    margin:0;
    border:0;
    box-sizing:border-box;
    text-decoration:none;
    list-style:none;
    line-height:140%;
    text-align:center;
    color:#757575;
    font-size:14px;
    font-family:arial,"Microsoft JhengHei","",sans-serif;
}
#hkep_msg_pop h1{
    font-size:20px;
    padding-bottom:4px;
}
#hkep_msg_pop b{
    font-weight:bold;
    color:#4f4f4f;
}
#hkep_msg_pop p{
    padding-top:8px;
}
#hkep_msg_pop #hkep_msg_pop_content>div{
    margin-bottom:24px;
}
#hkep_msg_pop img.alert-icon{
    width:60px;
    height:60px;
    margin-bottom:16px;
}




